<template>
  <!--用户注册表单-->
  <div>
    <section class="login">
      <div class="container">
        <div class="row justify-content-center">
          <div class="col-md-7 col-lg-5">
            <div class="login-wrap p-3 p-md-5">
              <a-form-model :model="formRegister" :rules="rules" @submit="register" ref="formRegister"
                            @submit.native.prevent>
                <a-form-model-item prop="username">
                  <a-input v-model="formRegister.username" placeholder="请输入用户名" size="large">
                    <a-icon slot="prefix" type="user" style="color:rgba(0,0,0,.25)"/>
                  </a-input>
                </a-form-model-item>
                <a-form-model-item prop="password">
                  <a-input v-model="formRegister.password" type="password" placeholder="请输入密码" size="large">
                    <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)"/>
                  </a-input>
                </a-form-model-item>
                <a-form-model-item prop="confirmPassword">
                  <a-input v-model="formRegister.confirmPassword" type="password" placeholder="请再次输入密码"
                           size="large">
                    <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)"/>
                  </a-input>
                </a-form-model-item>
                <a-form-model-item prop="phone">
                  <a-input v-model="formRegister.phone" placeholder="请输入手机号" size="large">
                    <a-icon slot="prefix" type="phone" style="color:rgba(0,0,0,.25)"/>
                  </a-input>
                </a-form-model-item>
                <a-form-model-item prop="code">
                  <a-input-search v-model.number="formRegister.code" placeholder="输入验证码" size="large"
                                  @search="sendCode">
                    <a-icon slot="prefix" type="code" style="color:rgba(0,0,0,.25)"/>
                    <a-button slot="enterButton" :disabled="disable">
                      {{ btnText }}
                    </a-button>
                  </a-input-search>
                </a-form-model-item>
                <a-form-model-item>
                  <a-button
                      type="primary" block
                      html-type="submit"
                      size="large"
                      :loading="loading"
                  >
                    注 册
                  </a-button>
                </a-form-model-item>
              </a-form-model>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
  import request from "@/utils/request";
  import storage from "store";

  export default {
    name: 'Register',
    data() {
      let validatorPhone = (rule, value, callback) => {
        // 正则判断手机号格式的格式，正则判断失败抛出错误，否则直接callback()
        if (!/^1[2-9]\d{9}$/.test(value)) {
          callback(new Error("手机号格式不正确!"));
        } else {
          callback();
        }
      }

      let checkCode = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('请输入验证码！'));
        }

        if (!Number.isInteger(value)) {
          callback(new Error('请输入数字！'));
        } else if (!/^\d{6}$/g.test(value)) {
          callback(new Error('验证码位数不正确！'));
        } else {
          callback();
        }
      }

      let validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码！'));
        } else if (!/^(?=.*[a-zA-Z])(?=.*[0-9])[A-Za-z0-9]{8,16}$/.test(value)) {
          callback(new Error('新密码必须由数字和字母构成，不能全部为数字，不能全部为字母，长度在8-16位之间'));
        } else if (this.formRegister.confirmPassword !== '') {
          this.$refs.formRegister.validateField('confirmPassword');
          callback();
        } else {
          callback();
        }

      }

      let validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入新密码！'));
        } else if (value !== this.formRegister.password) {
          callback(new Error("两次输入的密码不一致!"));
        } else {
          callback();
        }
      };

      let validateUserName = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入用户名！'));
        } else if (!/^[a-zA-Z][a-zA-Z0-9_]{5,15}$/.test(value)) {
          callback(new Error('用户名必须由字母开头，长度在6-16位之间，只能包含数字、字母、下划线'));
        } else {
          callback();
        }
      }

      return {
        formRegister: {
          username: '',
          password: '',
          confirmPassword: '',
          phone: '',
          code: ''
        },
        rules: {
          username: [
            {validator: validateUserName, trigger: 'change'}
          ],
          password: [{validator: validatePass, trigger: 'change'}],
          confirmPassword: [{validator: validatePass2, trigger: 'change'}],
          phone: [
            {required: true, message: '请输入手机号！', trigger: 'blur'},
            {validator: validatorPhone, trigger: 'change'}
          ],
          code: [{validator: checkCode, trigger: 'change'}]
        },
        loading: false,
        btnText: '获取验证码',
        disable: false,
      };
    },
    methods: {
      register() {
        this.$refs.formRegister.validate(valid => {
          if (!valid) {
            this.$message.error('请先规范填写表单！');
            return false;
          }

          this.loading = true;
          const {username, password, phone, code} = this.formRegister;
          
          request({
            method: 'post',
            url: '/ouser/before/register',
            data: {username: username, password: password, phone: phone, code: code}
          }).then(res => {
            if (res.code === 1) {
              storage.clearAll();
              this.$router.replace({name: 'registerSuccess'});
            } else {
              this.$message.error(res.msg);
            }
            this.loading = false;
          }).catch(error => {
            this.$message.error('服务器错误！');
            console.log(error);
          })
        })
      },
      sendCode() {
        this.$refs.formRegister.validateField(['phone'], error => {
          if (error) {
            this.$message.error('获取验证码失败！');
            return;
          }
          
          this.disable = true;
          let count = 60;
          let timer = setInterval(() => {
            this.btnText = count + ' 秒后重新发送';
            count--;
            if (count < 0) {
              this.disable = false;
              this.btnText = '获取验证码';
              clearInterval(timer);
            }
          }, 1000)

          request({
            method: 'get',
            url: '/ouser/before/getCode',
            params: {phone: this.formRegister.phone}
          }).then(res => {
            if (res.code === 1) {
              this.$message.success('验证码发送成功，请注意查收！', 5);
            } else {
              this.$message.error(res.msg);
            }
          }).catch(error => {
            this.$message.error('服务器错误！');
            console.log(error);
          })
        })
        
      },
    },
  }
</script>

<style scoped>
  .login {
    min-height: 720px;
    background-color: #f8f9fd;
  }

  .login-wrap {
    position: relative;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0px 10px 34px -15px rgb(0 0 0 / 24%);
  }

  .login-wrap h3 {
    font-weight: 350;
  }


  .col-lg-5 {
    flex: 0 0 auto;
    width: 35%;
  }


  .col h4 {
    text-align: center;
  }

  .active h4 {
    color: green;
    border-bottom: 2px solid green;
  }

  .ant-form {
    margin-top: 5%;
  }

  /deep/ .ant-input-lg {
    height: 50px;
  }

  .ant-btn-lg {
    height: 48px;
  }


  .form-group {
    margin-bottom: 1rem;
  }

  .ant-input-search-button {
    height: 50px;
  }

</style>